:["@use \"../../../style/mixins/breakpoints\";\n@use \"../../../style/mixins/transition\";\n@use \"../../../style/variables/typography\";\n\n.user-account-content {\n    display: flex;\n    flex-direction: row;\n    padding: 6px 8px 6px 8px;\n    width: auto;\n    color: #403f3f;\n    gap: 6px;\n    font-family: typography.$default-font;\n    font-size: 14px;\n    justify-content: space-between;\n    align-items: center;\n    margin: 0 6px 0 12px;\n    background: transparent;\n    &:hover {\n        color: #1073bc;\n        cursor: pointer;\n    }\n\n    border: none;\n    span:last-child {\n        display: none;\n        align-items: center;\n        flex-direction: row;\n        padding: 2px 1px;\n        vertical-align: middle;\n        font-weight: 500;\n        @include breakpoints.breakpoint(laptop) {\n            display: inline-block;\n        }\n    }\n    span:first-child {\n        svg {\n            height: 24px;\n            width: 24px;\n            vertical-align: middle;\n        }\n    }\n}\n.menu-wrapper {\n    font-size: 14px;\n    font-family: typography.$default-font;\n    position: relative;\n    @include transition.transition(all 0.3s ease-in-out);\n\n    > div {\n        max-height: 0;\n        overflow: hidden;\n        @include transition.transition(all 0.3s ease-in-out);\n        position: absolute;\n        display: flex;\n        flex-direction: column;\n        background-color: white;\n        box-shadow: 0px 1px 3px 0px #0000001a;\n        width: 375px;\n        z-index: 100;\n        top: 10px;\n        right: -85px;\n        > div:first-child > span {\n            display: block;\n            &:last-child {\n                font-weight: bolder;\n            }\n        }\n        ul {\n            list-style-type: none;\n            li {\n                margin: 12px 0;\n                a,\n                a:link,\n                a:visited {\n                    color: #1a589f;\n                }\n            }\n        }\n\n        > *:nth-child(odd) {\n            background-color: #f9f9f9;\n            padding: 32px;\n            display: flex;\n            flex-direction: column;\n            gap: 5px;\n        }\n        > *:nth-child(even) {\n            background-color: white;\n            margin-top: 10px;\n            display: flex;\n            flex-direction: column;\n            gap: 5px;\n            padding: 0 32px;\n        }\n        > *:last-child {\n            padding: 32px;\n        }\n        a,\n        a:link,\n        a:visited {\n            color: #000000;\n            display: flex;\n            text-decoration: underline;\n        }\n        a:hover {\n            cursor: pointer;\n        }\n        .menu-user-icon {\n            svg {\n                padding-left: 4px;\n                width: 24px;\n                height: 24px;\n                color: #000000;\n            }\n        }\n    }\n    &.open {\n        > div {\n            max-height: 1000px;\n        }\n    }\n}\n.user-account-container {\n    text-align: right;\n    background-color: #d2cecb;\n    max-height: 0;\n    overflow: hidden;\n    &.open {\n        max-height: 56px;\n    }\n    a,\n    button {\n        display: inline-block;\n        padding: 0.25em 1em 0.25em 1.1em;\n        margin: 15px 0;\n        text-decoration: none;\n        color: transparent;\n        cursor: pointer;\n        border: none;\n        font-weight: normal;\n        background: transparent;\n        &:not(:first-child) {\n            border: none;\n        }\n    }\n}\n","$default-font: \"Open Sans\", sans-serif;\n$header-font: \"Raleway\", sans-serif;\n$default-font-size: 15px;\n","@use \"../variables/breakpoints\";\n\n@mixin breakpoint($point) {\n    @if $point == desktop {\n        @media (min-width: breakpoints.$screen-desktop) {\n            @content;\n        }\n    } @else if $point == laptop {\n        @media (min-width: breakpoints.$screen-laptop) {\n            @content;\n        }\n    } @else if $point == tablet {\n        @media (min-width: breakpoints.$screen-tablet) {\n            @content;\n        }\n    } @else if $point == phablet {\n        @media (min-width: breakpoints.$screen-phablet) {\n            @content;\n        }\n    }\n}\n","﻿@mixin transition($transition...) {\n    -moz-transition: $transition;\n    -o-transition: $transition;\n    -webkit-transition: $transition;\n    transition: $transition;\n}\n\n@mixin transition-property($property...) {\n    -moz-transition-property: $property;\n    -o-transition-property: $property;\n    -webkit-transition-property: $property;\n    transition-property: $property;\n}\n\n@mixin transition-duration($duration...) {\n    -moz-transition-property: $duration;\n    -o-transition-property: $duration;\n    -webkit-transition-property: $duration;\n    transition-property: $duration;\n}\n\n@mixin transition-timing-function($timing...) {\n    -moz-transition-timing-function: $timing;\n    -o-transition-timing-function: $timing;\n    -webkit-transition-timing-function: $timing;\n    transition-timing-function: $timing;\n}\n\n@mixin transition-delay($delay...) {\n    -moz-transition-delay: $delay;\n    -o-transition-delay: $delay;\n    -webkit-transition-delay: $delay;\n    transition-delay: $delay;\n}\n"],"sourceRoot":""}]);
